import { useState, useEffect, useMemo } from 'react';
import { LineChart, Line, BarChart, Bar, PieChart, Pie, Cell, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer, AreaChart, Area } from 'recharts';
import { Search, Filter, TrendingUp, Users, FileText, Globe, Calendar, ChevronDown, ChevronUp, BookOpen, Building, Target, Zap, Eye, X, Menu, ArrowUp, ArrowDown, AlertCircle, ExternalLink, Info, Award, Clock } from 'lucide-react';
import NetworkGraph from './components/visualizations/NetworkGraph';
import SankeyDiagram from './components/visualizations/SankeyDiagram';
import { parseCSVData } from './utils/csvParser';
import { 
  computeYearDistribution, 
  computeDomainDistribution, 
  computeUsageDistribution, 
  computePublicationTypeDistribution,
  computeDomainEvolution
} from './utils/statisticsUtils';

// Enhanced TypeScript interface for the full CSV data structure
interface Publication {
  id: number;
  publication_type: string;
  title: string;
  authors: string;
  year: number;
  journal: string;
  publisher: string;
  usage_type: 'PRIMARY_ANALYSIS' | 'RESEARCH_ENABLER' | 'CONTEXTUAL_REFERENCE';
  usage_justification: string;
  usage_description: string;
  research_domain: string;
  geographic_focus: string;
  data_years_used: string;
  key_findings: string;
  policy_implications: string;
  doi_url: string;
  notes: string;
  // Computed fields for analysis
  quality_score?: number;
  policy_impact_score?: number;
  geographic_reach?: 'LOCAL' | 'STATE' | 'REGIONAL' | 'NATIONAL' | 'INTERNATIONAL';
  methodological_rigor?: 'HIGH' | 'MEDIUM' | 'LOW';
}

// NOTE: Data quality assessment functions are now in the csvParser utility
// These have been moved there to ensure consistent scoring across the app

// NOTE: CSV parsing is now handled by the dedicated csvParser utility
// which uses PapaParse for robust handling of complex CSV formats

// Transform hardcoded data to new structure (temporary until CSV integration)
